.contact {
  text-align: center;
}

.contact-container {
  max-width: 400px;
  margin: 20px auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
  
.contact-form {
  display: flex;
  flex-direction: column;
}
  
.contact-form label {
  margin-bottom: 5px;
  margin-left: 5px;
}
  
.contact-form input,
.contact-form textarea {
  margin-bottom: 10px;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.contact-form .subject, 
.contact-form .email {
    margin-left: 100px;
}
  
.contact-form button {
  background-color: #6B0000;
  color: #fff;
  padding: 10px;
  margin-left: 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
  
.contact-form button:hover {
    background-color: #000;
}  